*{
  padding: 0;
  margin:  0;
  box-sizing: border-box;
  background-color: #d2dbdd;
}

.heading_style{
  text-align: center;
  color:black;
  text-shadow: 0px 2px 4px #868686;
  text-transform: uppercase;
  font-family: 'Goblin One', cursive;
  font-size: 13px;
  font-weight: 500;
  background-color: #fff;
  font-size: 1.5rem;
  padding: 30px 0px;
  margin-bottom: 30px;
  letter-spacing: 2px;
}

.cards{
  width: 100%;
  height: auto;
}

.card{
  margin: 6%;
  background-color: #fff;
  width: 21.25%;
  border-radius: 12px;
  float: left;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
}

.card :hover{
  transform: scale(1.05,1.05);
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
}

.card_image{
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card_info{
  
  border-bottom-left-radius: 12px;
  border-top-right-radius: 12px;
  padding: 16px 24px 24px 24px;
  background-color: #fff;
}

.card_title{
 
  background-color: #fff;
  text-transform: capitalize;
  font-size: 13px;
  color: #868686;
  letter-spacing: 1px;
}

.card_name{
  margin-top: 5px;
  margin-bottom: 10px;
  text-transform: uppercase;
 background-color: #fff;
  font-size: 15px;
  font-weight: 800;
  letter-spacing: 2px;
  
}

.button{
  
  padding: 5px 8px;
  text-transform: uppercase;
  background-color: #fff;
  color: #868686;
  outline: none;
  border: 1px solid black;
  font-weight: 500;
  font-size: 13px;
  letter-spacing: 2px;
  cursor: pointer;
}


